export default {
  data() {
    return {
      scoller_fab: false
    }
  },
  methods: {
    onScroll(e) {
      if (typeof window === 'undefined') {
        return
      }
      const top = window.pageYOffset || e.target.scrollTop || 0
      this.scoller_fab = top > 20
    },
    toTop() {
      this.$vuetify.goTo(0)
    }
  }
}
